	module.exports = {"data":{"markdownRemark":{"html":"<h2>CSS Structure</h2>\n<p>A CSS rule follows this basic structure:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">selector</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">property</span><span class=\"token punctuation\">:</span> value<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">proerty</span><span class=\"token punctuation\">:</span> value<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>Everything inside the curly brackets styles whatever is selected by the <a href=\"https://guide.freecodecamp.org/css/selectors\">selector</a>. Inside a CSS rules is a set of <a href=\"https://guide.freecodecamp.org/css/properties\">property</a>/value pairs.</p>\n<p>Multiple CSS rules can be put in one CSS file - here is an example:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">h1</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">color</span><span class=\"token punctuation\">:</span> red<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token selector\">div</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">text-align</span><span class=\"token punctuation\">:</span> center<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">color</span><span class=\"token punctuation\">:</span> blue<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token selector\">img</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">margin-left</span><span class=\"token punctuation\">:</span> 2px<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">margin-top</span><span class=\"token punctuation\">:</span> 100px<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h4>More Information:</h4>\n<ul>\n<li><a href=\"https://codetheweb.blog/2017/11/11/css-syntax/\">CSS Syntax on Code The Web</a></li>\n<li>More on <a href=\"https://guide.freecodecamp.org/css/selectors\">CSS selectors</a></li>\n<li>More on <a href=\"https://guide.freecodecamp.org/css/properties\">CSS properties</a></li>\n</ul>","fields":{"slug":"/css/structure-of-css/"},"frontmatter":{"title":"Structure of CSS"}}},"pathContext":{"slug":"/css/structure-of-css/"}}

